body, h1, h2, h3, h4, h5, h6, hr, p, blockquote, dl, dt, dd, ul, ol, li, pre, form, fieldset, legend, button, input, textarea, th, td { margin:0; padding:0; } 
body, button, input, select, textarea { font:12px/1.5tahoma, arial, \5b8b\4f53; } 
h1, h2, h3, h4, h5, h6{ font-size:100%; } 
address, cite, dfn, em, var { font-style:normal; } 
code, kbd, pre, samp { font-family:couriernew, courier, monospace; } 
small{ font-size:12px; } 
ul, ol { list-style:none; } 
a { text-decoration:none; } 
a:hover { text-decoration:underline; } 
sup { vertical-align:text-top; } 
sub{ vertical-align:text-bottom; } 
legend { color:#000; } 
fieldset, img { border:0; } 
button, input, select, textarea { font-size:100%; } 
table { border-collapse:collapse; border-spacing:0; }

html, body{min-width: 1206px; height: 100%; font-size: 62.5%; font-family: Lato, "Microsoft Jhenghei", "Hiragino Sans GB", "Microsoft YaHei", sans-serif !important;}
/* 设置背景图 */
body {
  background-image: url(/static/media/bg.88182470.jpg);
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-size: 100% 100%;
}

.container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
}

/* 设置github头像 */
.github {
  margin-top: 2rem;
  margin-right: 2rem;
  position: fixed;
  width: 100%;
  text-align: right;
  padding-right: 2rem;
}

.github a .antd-github {
  font-size: 3.6rem;
}

.search-box{
  margin-top: 10rem;
  margin-bottom: 5rem;
  text-align: center;
}

/* logo部分 */
img#logo {
  width: 50%;
}

img#logo:hover {
  -webkit-transform: rotate(-2deg) scale(1.02);
          transform: rotate(-2deg) scale(1.02);
  cursor: pointer;
}

/* 输入框 */
.antd-input {
  margin-top: 3rem;
  width: 80%;
}

/* 导航部分, 分为导航菜单和站点列表两个部分 */
.nav-box{
  width: 60%;
  background-color: rgba(255, 255, 255, 0.1);
}

/* 导航菜单部分 */
.menu {
  width: 100%;
  height: 3.2em;
  background-color: rgba(0, 175, 236, 0.8);
  text-align: center;
  font-size: 1.6rem;
  color: white;
  list-style: none;
  position: relative;
}

.menu li {
  width: 6em;
  display: inline-block;
  line-height: 3.2em;
  cursor: pointer;
  border-right: 1px solid rgba(0,0,0,0.1);
}

.menu li:first-child {
  border-left: 1px solid rgba(0,0,0,0.1);
}

.menu li:hover {
  color: green;
}

/* 三角形 */
.menu .triangle {
  display: inline-block;
  width: 0;
  border-bottom: 0.7em solid rgba(214, 218, 230);
  border-left: 0.7em solid transparent;
  border-right: 0.7em solid transparent;
  position: absolute;
  top: calc(3.2em - 0.7em);
  left: calc(50% - 0.7em);
}

/* 站点列表 */
.items {
  padding-left: 4em;
  padding-bottom: 1.4em;
  min-height: 17.6em;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  font-size: 1.6rem;
}

/* 清除a标签的默认样式 */
.items a {
  color: inherit;
  text-decoration: none;
  margin-top: 1.4em;
  margin-right: 4em;
}

.items a li{
  width: 12rem;
  height: 3em;
  line-height: 3em;
  text-align: center;
  border-radius: 15%;
  background-color: rgba(255,255,255,0.6);
}

.items a li:hover {
  cursor: pointer;
  color: rgb(224, 226, 188);
  background-color: rgba(0, 175, 236, 0.8);
}
/* 公共的css样式 */
